<template>
	<div class="hangman">
		<div>
			<img class="hangman-img" alt="manhang" :src="`hangmen/man${index}.png`">
			<div class="wordbox flex">
				<div class="flex-item" v-for="(v, key) in sorted_failures" :key="key">
					<span>
						{{v}}
					</span>
				</div>
			</div>
		</div>
		<p class="hangman-phrase">{{filtered_word}}</p>
		<div>
			<form>
				<input ref="hangmanInput" :disabled="has_won || has_lost" placeholder="Put a letter here" v-model="inputString"/>
				<button :disabled="has_won || has_lost || inputString.length == 0" @click="letter">{{submit_text}}</button>
			</form>
		</div>

		<button @click="reset()">Reset</button>
	</div>
</template>

<script>
// @ is an alias to /src
import { filter } from 'minimatch';
import { setTimeout } from 'timers';

export default {
	name: 'home',
	computed: {
		sorted_failures(){
			return this.failed_letters.sort();
		},
		submit_text(){
			if (this.inputString.length > 1)
			{
				return "Guess Phrase"
			}
			return "Submit Letter"
		},

		has_lost(){
			return this.index >= 9;
		},
		has_won(){
			for (var i = 0; i < this.word.length; ++i)
			{
				let char = this.word[i];
				// inefficient loop, but this is a game jam
				if (char == ' ')
				{
					continue;
				}
				else if (this.letters[char.toLowerCase()])
				{
					continue;
				}
				else
				{
					return false;
				}
			}
			return true;
		},
		index(){
			return Math.min(this.failed_letters.length, 9);
		},
		lower_word(){
			return this.word.toLowerCase().trim();
		},
		filtered_word(){
			let filtered = ""
			for (var i = 0; i < this.word.length; ++i)
			{
				let char = this.word[i];
				if (char == ' ')
				{
					filtered += '    ';
				}
				else if (this.letters[char.toLowerCase()])
				{
					filtered += this.word[i] + ' ';
				}
				else
				{
					filtered += "_ ";
				}
			}
			return filtered;
		}
	},
	data(){
		return {
			damage: 0,
			letters: {},
			failed_letters: [],
			word: "Floop doop derp",
			inputString: ""
		}
	},
	methods: {
		reset() {
			let v = prompt("Give me a word bitch")
			if (v == null)
			{
				return;
			}

			this.damage = 0;
			this.$set(this, 'letters', {});
			this.word = "Floop doop derp";
			this.inputString = "";
			this.$set(this, "failed_letters", [])

			if (v != null && v != "" && v.trim() != "")
			{
				this.word = v;
				for(var i = 0; i < this.word.length; ++i)
				{
					let char = this.word[i].toLowerCase();
					if (char.match(/[^A-Za-z ]/))
					{
						this.$set(this.letters, char, true);
					}
				}
				this.$refs.hangmanInput.focus();
			}
			else
			{
				this.reset();
			}
			setTimeout(() => {
				this.$refs.hangmanInput.focus();
			}, 0);
		},
		letter() {
			let str = this.inputString.trim().toLowerCase();
			if (str.length == 0)
			{
				alert('You need to put in a letter, dumbass');
				return
			}
			else if (str.length > 1)
			{
				console.log(`${str} =? ${this.word.toLowerCase().trim()}`)
				if (str == this.word.toLowerCase().trim())
				{
					for (var i = 0; i < this.word.length; ++i)
					{
						this.$set(this.letters, this.word[i].toLowerCase(), true)
					}
					setTimeout(()=>{
						alert("You have won!");
					}, 0)
					return;
				}
				setTimeout(()=>{
					alert("Your guess is incorrect");
				}, 0)
				return;
			}
			
			let char = str[0];
			if (char.match(/[^A-Za-z ]/))
			{
				alert('That isn\'t a letter, dumbass');
				this.$set(this, 'inputString', "");
				return;
			}
			if (this.letters[char])
			{
				alert('You\'ve already used this letter, dumbass')
				this.$set(this, 'inputString', "");
				return;
			}

			this.$set(this.letters, char, true);
			if (!this.lower_word.includes(char))
			{
				this.failed_letters.push(char);
			}

			if (this.has_won)
			{
				setTimeout(()=>{
					alert("You have won!");
				}, 0)
				return;
			}

			if (this.has_lost)
			{
				setTimeout(()=>{
					alert("You have lost!");
				}, 0)
				for (var i = 0; i < this.word.length; ++i)
				{
					this.$set(this.letters, this.word[i].toLowerCase(), true)
				}
				return;
			}

			this.$set(this, 'inputString', "");
		}
	},
	mounted() {
		this.reset();
	}
}
</script>

