{
	width: 30%;
}

.flex
{
	margin-left: auto;
	margin-right: auto;
	display: flex;
	flex-direction: row;
	justify-content: center;
}

.flex-item
{
	background-color: rgba(255, 230, 230, 0.5);
	box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
	margin: 10px;
	font-size: 30px;
}

.hangman-img
{
	margin: auto;
	width: 50%;
}

.hangman-phrase
{
	white-space: pre;
	font-size: 30px;
}
</style>
